t<template>
  <div class="p-5 ">
      <p class="pl-4 text-2xl">BERITA</p>
      <hr class="style1">
      <div class="row">
          <div class="col-4 ">
            <div class="mb-4">
            <label class="block text-grey-darker text-sm font-bold mb-2" for="SearchItem"><span class="typcn typcn-zoom"></span> Search : </label>
            <input
                id="SearchItem"
                placeholder="Search Item"
                class="shadow appearance-none border rounded w-full py-2 px-3 text-grey-darker leading-tight focus:outline-none focus:shadow-outline"
                type="text"
                v-model="searchItem"
            />
            </div>
          </div>
      </div>
      <div v-for="(item, index) in filteredData" :key="index+'berita'" @click="$router.push(`/sekolah/depan/${item.id_berita}`)" class="shadow-lg rounded-lg p-3 cursor-pointer anim hover:bg-grey-light mt-4" >
          <div class="row">
              <div class="col-12  bg-white p-5 rounded-lg shadow-lg">
                  <img :src="`https://infolayanans.space/${item.gambar}`" class="kinoLightBox img-fluid float-left mr-4" style="border-radius: 20px;height:150px;width:200px;" alt="top">
                  <br>
                <p class=" pl-2 font-bold font-times text-capitalize text-xl">{{item.judul}}</p>
                <p class=" pl-2 text-sm  text-sm font-times font-italic">Posted pada Tanggal : <span class="text-green">{{format(item.tanggal)}}</span> &nbsp;&nbsp;&nbsp;&nbsp;
                 oleh : 
                <span class="font-bold">
                 {{item.author}}
                </span>
                 </p>
                 <br>
                <p class="  pl-3 text-justify text-capitalize text-sm" style="" v-html="truncate(item.isi_berita)"></p>
                <p class="font-bold text-red font-italic">Selanjutnya</p>
              </div>
          </div>
      </div>
      <br>
    <div class="container-fluid">
      <div class="row">
        <div class="offset-8 col-sm-4 ">
          <button
            type="button"
            v-for="(item, index) in tombolPagination"
            @click="changePagination(item)"
            :key="index+'pagination2'"
            v-show="item<jumlahPagination+1"
            class="btn btn-sm btn-warning rounded-circle"
            :class="{'bg-blue hover:bg-blue border-blue cursor-not-allowed text-red':item==pagination}"
            :disabled="item==pagination"
          >{{item}}</button>
        </div>
      </div>
    </div>
    <button type="button" @click="pagPrev" class="btn btn-sm btn-dark font-times"><span class="typcn typcn-arrow-left"></span> Previous</button>
    <button type="button" @click="pagNext" class="btn btn-sm btn-dark font-times float-right"><span class="typcn typcn-arrow-right"></span> Next</button>
  </div>
</template>

<script>
var id = require("date-fns/locale/id");
import axios from "axios";
export default {
    layout:"awal",
  data() {
    return {
      datanya: "",
      temp1: 0,
      temp2: 10,
      pagination: 1,
      pembagiannya: 10,
      orderBy: "",
      orderWith: true,
      searchItem: ""
    };
  },
  computed: {
    jumlahPagination() {
      let data = this.datanya;
      return data.length / 10;
    },
    tombolPagination() {
      let data = this.pagination;
      // v-if != (current > max ) button nya
      let btn1 = data - 2;
      let btn2 = data - 1;
      let btn3 = data;
      let btn4 = data + 1;
      let btn5 = data + 2;
      let array = [btn1, btn2, btn3, btn4, btn5];
      if (array.indexOf(-1) != -1) {
        array = [1, 2, 3, 4, 5];
      } else if (array.indexOf(0) != -1) {
        array = [1, 2, 3, 4, 5];
      }
      return array;
    },
    filteredData() {
      let that = this;
      let data = this.datanya;
      //! order fungsi
      if (this.orderBy.length > 0) {
        data = this.$_.orderBy(data, ["title"], [this.orderBy]);
      }
      //! search fungsi
      if (data[0] != undefined) {
        let keys = Object.keys(data[0]);
        data = data.filter((v, i, a) => {
          let hasil = false;
          keys.filter(key => {
            if (typeof v[key] === "string") {
              if (
                v[key].toLowerCase().indexOf(this.searchItem.toLowerCase()) !=
                -1
              ) {
                hasil = true;
              }
            } else {
              if (v[key].toString().indexOf(this.searchItem) != -1) {
                hasil = true;
              }
            }
          });
          if (hasil) return v;
        });
      }
      // data = data.map(e => {
      //   let obj = {};
      //   let kunci = Object.keys(e);
      //   kunci = that.$_.difference(kunci, that.unless);
      //   kunci.forEach((a, index) => {
      //     if (kunci.find(e => e == that.date[index])) {
      //     } else {
      //       // obj[a] = 'tanggal'
      //     }
      //     console.log(typeof e[a]);
      //     obj[a] =
      //       typeof e[a] != "string"
      //         ? e[a]
      //         : new Date(e[a]) !== "Invalid Date" && !isNaN(new Date(e[a]))
      //         ? that.format(e[a])
      //         : e[a];
      //   });
      //   return obj;
      // });
      data = data.slice(this.temp1, this.temp2);
      return data;
    }
  },
  methods: {
      truncate(value) {
      if (value.length > 20) {
        value = value.substring(0, 207) + " ...";
      }
      return value;
    },
    format(date) {
      return this.$datefns.format(
        new Date(date),
        "dddd DD MMMM YYYY",
        {
          locale: id
        }
      );
    },
    pagPrev() {
      this.pagination = this.pagination - 1;
      if (this.pagination == 0) {
        this.pagination = 1;
      }
      this.temp2 = this.pagination * this.pembagiannya;
      this.temp1 = Math.ceil(this.temp2) - this.pembagiannya;
    },
    pagNext() {
      this.pagination = this.pagination + 1;
      if (this.pagination > (this.jumlahPagination+1)) {
        this.pagination = Math.ceil(this.jumlahPagination);
      }
      this.temp2 = this.pagination * this.pembagiannya;
      this.temp1 = Math.ceil(this.temp2) - this.pembagiannya;
    },
    changePagination(pag) {
      this.pagination = pag;
      this.temp2 = this.pagination * this.pembagiannya;
      this.temp2 = Math.ceil(this.temp2);
      this.temp1 = this.temp2 - this.pembagiannya;
      this.temp1 = Math.ceil(this.temp1);
    }
  },
  mounted() {
      let fd = new FormData()
      fd.append('data', `select * from sekolah_berita`)//database setting di server
      fd.append('database','infolay3_test')
      axios.post('https://infolayanans.space/api/mysql/auto.php', fd
      , {
          headers: {
          Authorization: localStorage.getItem('auth._token.local')
          }
      }).then(res => {
          console.log(res)
          this.datanya=res.data
          this.$forceUpdate()
      })
  }
};
</script>
